.btn {
  min-width: 200px;
  min-height: 42px;
  color: var(--white-color);
  text-align: center;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.19;
  display: block;
  position: relative;
  overflow: hidden;
}

.btn--modal {
  min-width: 232px;
  min-height: 42px;
}

.btn--footer {
  min-width: 181px;
  min-height: 42px;
  text-transform: uppercase;
}

.btn:after {
  content: "";
  width: calc(100% - 2px);
  height: calc(100% - 2px);
  background-color: var(--dark-background-color);
  opacity: 0;
  border-radius: 5px;
  transition: all .2s;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scaleX(0);
}

.btn:before {
  content: "";
  width: 100%;
  height: 100%;
  background-image: var(--gradient--background);
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn:hover:after, .btn:focus:after {
  opacity: 1;
  transform: translate(-50%, -50%)scaleX(1);
}

.btn__txt {
  z-index: 10;
  position: relative;
}

.hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

