a {
    text-decoration: none;
}

input {
    background: none;
    outline: none;
}


h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

ul,
ol {
    margin: 0;
    padding: 0;
    list-style: none;
}

img {
    display: block;
    width: 100%;
    height: auto;
}

body{font-family: 'Inter', sans-serif;}